*{
    margin: 0;
    padding: 0;
}

h1{
    font-size: 35px;
    text-align: center;
}

p{
    font-size: 18px;
    text-align: justify;
}

a.navbar-brand {
    display:flex;
    text-align: left;
    color: rgb(0, 0, 0);
}

a.navbar-brand:hover{
    color: rgb(0, 0, 0);
}

a.nav-link:hover{
    color: rgb(0, 0, 0);;
}

a.nav-link:visited{
    color: rgb(0, 0, 0);;
}

.navbar{
    height:100px;
    font-size: 20px;
}


.navbar-inner {
    height: 100px;
    position: sticky;
    top: 0;
}

.navbar-outer {
    display: flex;
    align-items: center;
    position: fixed;
    height: 190px;
    top: -70px;
}

.scroll_navbar .navbar {
    background-color: rgba(255, 255, 255);
    animation: 1s show ease;
}

@keyframes show {
    from { opacity: 0; }
    to { opacity: 1; }
  }

.text-title{
    display:flex;
    text-align: left;
    align-items: center;
    align-self: center;
    justify-content: center;
    color: rgb(0, 0, 0);
}

.offcanvas {
    --bs-offcanvas-width: 100%;
    align-items: center;
    }
.offcanvas-body{
    font-size: 25px;
    text-align: center;
    padding-top: 50%;
    margin-left: auto;
    margin-right: auto;
}   

.caption{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url("illustration/DesktopBg.svg");
    background-repeat: no-repeat;
    background-size: cover;
}

.caption p{
    max-width: 60vw;
    text-align: justify;
}

.leftText{
    padding: 15px;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.leftText div{
    padding: 5px;
}

.leftText img {
    height: 150px;
    width: 150px;
}

.rightText{
    width: 100%;
    padding: 15px;
    justify-content: center;
    align-items: center;
}

.rightText div{
    padding: 5px;
}

.rightText img{
    height: 150px;
    width: 150px;
}

.TextGraph{
    width: 100%;
    padding: 15px;
    justify-content: center;
    align-items: center;
}

.jumbltorn{
    padding: 45px;
}

.jumbltorn * {
    text-align: left;
}


.white{
    background-color: #ffffff;
    color: #000000;
}

.white a{
    border: solid 2px 0;
    border-radius: 10px;
    background-color: #8e9de7;
    padding: 10px;
    text-decoration: none;
    color: white;

}

.blue{
    background-color: #8e9de7;
    color: #ffffff;
}

.blue a{
    border: solid 2px 0;
    border-radius: 10px;
    background-color: #ffffff;
    padding: 10px;
    text-decoration: none;
}

.Navbar{
    background-color: #ffffff;
    position: fixed;
    width: 100%;
}

.Navbar a{
    color: rgb(0, 0, 0);
}

.Navbar a:hover{
    color: rgba(0, 0, 0, 0.5);
}

.slider div {
    justify-content: center;
    text-align: center;
}

.slider img{
    width: 150px;
    height: 150px;
}

.slider .info{
    display: flex;
    flex-direction: column;
}
.slider * {
    --organic-arrow-thickness: 4px;
    --organic-arrow-height: 40px;
    --slider-height-percentage: 70%;
    --loader-bar-color: #ffffff;
    --loader-bar-height: 6px;
    --control-button-width: 10%;
    --control-button-height: 25%;
    
    --control-button-hover-opacity: 0.75;
    --control-button-background: transparent;
    --transition-bezier: cubic-bezier(0.5, 0.075, 0.25, 0.95);
    --slider-transition-duration: 575ms;
    --organic-arrow-color: #000000;
    --organic-arrow-border-radius: 0;
    --control-bullet-color: #000000;
    --control-bullet-active-color: #ffffff;
    --content-background-color: #ffffff;
    max-height: 50vh;
    text-align: center;
}
  
.directionList{
    margin-right: 0px;
}

.dirElement{
    padding: 5px;
}

.dirElement canvas {
    max-width: 100vw;
    max-height: 70px;
}

.dirElement a {
    text-decoration: underline;
    font-size: 20px;
    color: black;
}

.dirElement a:hover{
    cursor: pointer;
}

.direction li{ 
    list-style: none;
}

.direction li:before {
    content: "â– ";
    padding-right: 5px;
    color: rgb(0,191,255)s;
  }
  
  .direction li:nth-child(1):before {
    color: rgb(0,191,255);
  }
  
  .direction li:nth-child(2):before {
    color: rgb(30,144,255);
  }
  
  .direction li:nth-child(3):before {
    color: rgb(65,105,225);
  }
  .direction li:nth-child(4):before {
    color: rgb(0,0,255);
  }
  .direction li:nth-child(5):before {
    color: rgb(0,0,205);
  }
  .direction li:nth-child(6):before {
    color: rgb(0,40,255);
  }